import { Component, OnInit, OnDestroy } from "@angular/core";
import { employeeRaw } from "./data/employeeRaw";
import { Position } from "./data/position";
import { EmployeeService } from "./data/employee.service";
import { ActivatedRoute } from "@angular/router";
import { PositionService } from "./data/position.service";

@Component({
  selector: "app-employee",
  templateUrl: "./employee.component.html",
  styleUrls: ["./employee.component.css"]
})
export class EmployeeComponent implements OnInit, OnDestroy {
  private paramSubscription: any;
  private employeeSubscription: any;
  private getPositionsSubscription: any;
  private saveEmployeeSubscription: any;
  employee: employeeRaw;
  private positions: Position[];
  private succesMessage = false;
  private failMessage = false;

  constructor(
    private employeeService: EmployeeService,
    private activatedRoute: ActivatedRoute,
    private positionService: PositionService
  ) {}

  ngOnInit() {
    this.paramSubscription = this.activatedRoute.params.subscribe(params => {
      this.employeeSubscription = this.employeeService
        .getEmployee(params["_id"])
        .subscribe(emp => {
          this.employee = emp[0];

          this.getPositionsSubscription = this.positionService
            .getPositions()
            .subscribe(data => {
              this.positions = data;
            });
        });
    });
  }

  onSubmit() {
    this.saveEmployeeSubscription = this.employeeService
      .saveEmployees(this.employee)
      .subscribe(
        () => {
          this.succesMessage = true;
          setTimeout(() => {
            this.succesMessage = false;
          }, 2500);
        },
        () => {
          this.failMessage = true;
          setTimeout(() => {
            this.failMessage = false;
          }, 2500);
        }
      );
  }
  ngOnDestroy() {
    if (this.paramSubscription) {
      this.paramSubscription.unsubscribe();
    }
    if (this.employeeSubscription) {
      this.employeeSubscription.unsubscribe();
    }
    if (this.getPositionsSubscription) {
      this.getPositionsSubscription.unsubscribe();
    }
    if (this.saveEmployeeSubscription) {
      this.saveEmployeeSubscription.unsubscribe();
    }
  }
}
