import { Component, OnInit } from "@angular/core";
import { Employee } from "./data/employee";
import { EmployeeService } from "./data/employee.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-employees",
  templateUrl: "./employees.component.html",
  styleUrls: ["./employees.component.css"]
})
export class EmployeesComponent implements OnInit {
  private employees: Employee[];
  private getEmployeesSub: any;
  private loadingError: boolean = false;
  filteredEmployees: Employee[];

  constructor(
    private _employeeService: EmployeeService,
    private router: Router
  ) {}

  ngOnInit() {
    this.getEmployeesSub = this._employeeService.getEmployees().subscribe(
      data => {
        this.employees = data;
        this.filteredEmployees = data;
      },
      () => {
        this.loadingError = true;
      }
    );
  }

  routeEmployee(id: string) {
    this.router.navigate(["/employee/", id]);
  }

  onEmployeeSearchKeyUP(event: any) {
    let substring: string = event.target.value.toLowerCase();
    this.filteredEmployees = this.employees.filter(
      em =>
        em.FirstName.toLowerCase().indexOf(substring) != -1 ||
        em.LastName.toLowerCase().indexOf(substring) != -1 ||
        em.Position["PositionName"].toLowerCase().indexOf(substring) != -1
    );
  }

  ngOnDestroy() {
    if (this.getEmployeesSub != undefined) this.getEmployeesSub.unsubscribe;
  }
}
