import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { Employee } from "./employee";
import { employeeRaw } from "./employeeRaw";
import { HttpClient } from "@angular/common/http";

@Injectable({
  providedIn: "root"
})
export class EmployeeService {
  private url = "https://still-meadow-15953.herokuapp.com";

  constructor(private http: HttpClient) {}

  getEmployees(): Observable<Employee[]> {
    return this.http.get<Employee[]>(`${this.url}/employees`);
  }
  saveEmployees(employee: employeeRaw): Observable<any> {
    return this.http.put<any>(`${this.url}/employees/_id`, employee);
  }
  getEmployee(id): Observable<employeeRaw[]> {
    return this.http.get<employeeRaw[]>(`${this.url}/employee-raw/id`);
  }
}
